<template>
  <div ref="wrapper">
    <div>
      <div v-if="pulldown" class="pulldown">  
          <div class="clear" v-if="dragTip.showLoding">  
            <!-- <div class="fl"><img width="30" src="~common/js/loading9.svg"></div>   -->
            <div class="fl lh30 ml10">{{dragTip.text}}</div>  
          </div>  
        </div>  
      <slot></slot>
      <div v-if="pullup" class="pullup">  
          <div class="clear" v-if="!isDone">  
            <!-- <div class="fl"><img width="30" src="~common/js/loading10.svg"></div>   -->
            <div class="fl lh30 ml10"><loading></loading></div>  
          </div>  
          <div class="list-donetip" v-if="!isLoading && isDone">  
              <slot name="doneTip"></slot>  
          </div>  
      </div>
    </div>  
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  import Loading from '../loading1/loading'
  import { getRect } from '@/assets/js/dom'
  export default {
    props: {
      probeType: {
        type: Number,
        default: 1
      },
      click: {
        type: Boolean,
        default: true
      },
      listenScroll: {
        type: Boolean,
        default: false
      },
      data: {
        type: Array,
        default: null
      },
      pullup: {
        type: Boolean,
        default: false
      },
       pulldown:{  
        type:Boolean,  
        default:false  
      },  
      beforeScroll: {
        type: Boolean,
        default: false
      },
      refreshDelay: {
        type: Number,
        default: 20
      }
    },
    data(){
      return{
        dragTip:{  
        text:"",  
        showLoding:false  
      },  
      isLoading: false,  
      isDone: false,  
      }
    },
    components:{
      Loading
    },
    mounted() {
      setTimeout(() => {
        this._initScroll()
      }, 20)
    },
    methods: {
      _initScroll() {
        if (!this.$refs.wrapper) {
          return
        }
        if (this.$refs.wrapper) {
          this.$refs.wrapper.style.minHeight = `${getRect(this.$refs.wrapper).height + 1}px`
        }
        this.scroll = new BScroll(this.$refs.wrapper, {
          probeType: this.probeType,
          click: this.click
        })

        if (this.listenScroll) {
          let me = this
          this.scroll.on('scroll', (pos) => {
            me.$emit('scroll', pos)
          })
        }
        //   
        if(this.pullup){  
          this.scroll.on('scrollEnd',() => {  
            if(this.scroll.y <= (this.scroll.maxScrollY+50)){  
              //  
              this.$on('infinitescroll.loadedDone', () => {  
                  this.isLoading = false;  
                  this.isDone = true;  
              });  
              //  
              this.$on('infinitescroll.finishLoad', (ret) => {  
                  this.isLoading = false;  
              });  
              //  
              this.$on('infinitescroll.reInit', () => {  
                  this.isLoading = false;  
                  this.isDone = false;  
              });  
              this.$emit('scrollToEnd')  
            }  
          })  
        }  

         //   
        if(this.pulldown){  
          this.scroll.on('scroll',(pos) => {  
            //loding  
            this.dragTip.showLoding = true  
            //loding  
            this.isLoading = false  
            if(pos.y > 50){  
              this.dragTip.text = ""  
            }  
          })  
          this.scroll.on('touchEnd',(pos) => {  
            if(pos.y > 50){  
              this.dragTip.translate = 0  
              this.dragTip.text = "..."  
              //  
              this.$on('pullrefresh.finishLoad', this.resetParams);  
              this.$emit('pulldown',pos)  
            }  
          })  
        }  

        if (this.beforeScroll) {
          this.scroll.on('beforeScrollStart', () => {
            this.$emit('beforeScroll')
          })
        }
      },
      resetParams(){  
        setTimeout(() => {  
          this.isLoading = false;  
          this.isDone = false;  
          this.dragTip = {  
            text:"",  
            showLoding:false  
          }  
        },600)  
      },  
      disable() {
        this.scroll && this.scroll.disable()
      },
      enable() {
        this.scroll && this.scroll.enable()
      },
      refresh() {
        this.scroll && this.scroll.refresh()
      },
      scrollTo() {
        this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
      },
      scrollToElement() {
        this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
      }
    },
    watch: {
      data() {
        setTimeout(() => {
          this.refresh();
          new BScroll(this.$refs.wrapper,{  
            probeType:this.probeType,  
            click:this.click
          })  
        }, this.refreshDelay)
      }
    }
  }
</script>


