<template>
    <section class="m-list" ref="car-list">
        <ul v-if="carArr.length">
            <li class="car-item" v-for="(item,index) in carArr" :key="index">
                <div class="car-img">
                    <img :src="item.icon" alt="">
                </div>
                <div class="car-desc">
                    <div class="car-name">{{`${item.brand}`}}</div>
                    <div class="car-name">{{`${item.carName}`}}</div>
                    <div class="car-price" v-if="carType == 2">
                        <span>{{`${item.nowPrice}`}}</span>
                        <span>{{`${item.markPrice}`}}</span>
                    </div>
                    <div class="car-price" v-if="carType == 1">
                        <p>{{`${item.guidePrice}`}}</p>
                        <div class="car-labels" v-if = "item.labels">
                            <span class="label" v-for="(item,index) in getlabel(item.labels)" :key="index">
                                {{item}}
                            </span>
                        </div>
                        <span><del>{{`${item.firstPayment}`}}</del></span>
                        <span>{{`${item.monthPayment}`}}</span>
                    </div>
                    <div class="car-price" v-if="carType == 3">
                        <p class="car-msg">{{`${item.city}|${item.date}|${item.kilometers}`}}</p>
                        <span><del>{{`${item.firstPayment}`}}</del></span>
                        <span>{{`${item.monthPayment}`}}</span>
                    </div>                          
                </div>
            </li>
        </ul>  
    </section>
</template>

<script>
export default {
    props:{
        carArr:{
            type:Array,
            default:[]
        },
        carType:{
            type:Number,
            default:1
        }
    },
    methods:{
        selectItem(item, index) {
            this.$emit('select', item, index)
        },
        getlabel:function(labels){
            return labels.split(",");
        }
    }
}
</script>



