<template>
    <div class="weui-panel weui-panel_access">
        <div class="weui-panel__hd" v-if="title">{{title}}<a class="more" v-if="id"><i class="icon-arrow"></i></a></div>
        <div class="weui-panel__bd">
            <slot></slot>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        title:String,
        id:Number
    }
}
</script>



