<template>
    <div id="slider" class="carousel slide" data-ride="carousel">
        <!-- Indicators -->
        <ol class="carousel-indicators" v-if="slideArr.length">
            <li v-for="(item,index) in slideArr" data-target="#slider" :data-slide-to ="index" :key="index" :class="(index == 0 ? activeClass : '')" ></li>
        </ol>
        <!-- Wrapper for slides -->
        <div class="carousel-inner" role="listbox" v-if="slideArr.length">
            <div v-for=" ( item , index) in slideArr " :key="index" class="item" :class="[index == 0 ? activeClass : '']">
                <img :src = "item.imgUrl" class="img-responsive" alt="">
            </div>
        </div>
        <!-- Controls -->
        <a class="left carousel-control" href="#slider" role="button" data-slide="prev">
            <span class="glyphicon glyphicon-chevron-left" aria-hidden="true"></span>
            <span class="sr-only">Previous</span>
        </a>
        <a class="right carousel-control" href="#slider" role="button" data-slide="next">
            <span class="glyphicon glyphicon-chevron-right" aria-hidden="true"></span>
            <span class="sr-only">Next</span>
        </a>
    </div>
</template>

<script>
export default {
    data:function(){
        return{
            activeClass:"active"
        }
    },
    props:{
        slideArr:Array       
    }  
}
</script>


