<template>
    <div class="row">
        <div class="col-xs-12">
            <nav aria-label="Page navigation" class="page-navigation" v-if="computedPageArr.length >= 2">
                <ul class="pagination">
                    <li>
                        <a  aria-label="Previous" @click="prevClick">
                            <span aria-hidden="true"></span>
                        </a>
                        </li>
                        <li v-for="(item,index) in computedPageArr" :key="index" class="page-item" :class="index == 0 ?'active':''" @click="pageClick">
                            <a :data-pageNum ="item">{{item}}</a>
                        </li>
                        <li>
                        <a aria-label="Next" @click="nextClick">
                            <span aria-hidden="true"></span>
                        </a>
                    </li>
                </ul>
            </nav>
        </div>
    </div>
    
</template>

<script>
export default {
    data:function(){
        return {
            activePage:1
        }
    },
    props:{
        totalcount:Number       
    },
    methods:{
        pageClick:function(e){
            let a = e.target;
            $(".page-item").removeClass("active");
            $(a).parent().addClass("active");
            let pageNum = a.getAttribute("data-pageNum");
            this.activePage = pageNum;
        },
        prevClick:function(){
            let activePage = $(".page-item.active").find("a").data("pagenum");
            let activeLi = $(".page-item.active");
            if(activePage == 1){
                return;
            }else{
                this.activePage = activePage-1;
                 $(".page-item").removeClass("active");
                 activeLi.prev(".page-item").addClass("active");
            }
        },
        nextClick:function(){
            let activePage = $(".page-item.active").find("a").data("pagenum");
            let activeLi = $(".page-item.active");
            let totalPage = this.computedPageArr.length;
            if(activePage == totalPage){
                return;
            }else{
                this.activePage = activePage+1;
                $(".page-item").removeClass("active");
                activeLi.next(".page-item").addClass("active");
            }
        },
        getPageNum:function(curVal,oldVal){
            console.log(curVal)
            this.$emit("getPage",this.activePage); 
        }
    },
    watch:{
        activePage:'getPageNum'
    },
    computed:{
        computedPageArr:function(){
            let pageNum = Math.ceil(this.totalcount/12);
            let pageArr = [];
            for(let i=1;i<=pageNum;i++){
                pageArr.push(i);
            };
            return pageArr;
        }
    }
}
</script>



