<template>
    <header class="header">
        <div class="back" v-show="type == 2" @click="back"><i class="icon-cycle-arrow"></i></div>
        <div class="header-title"><slot></slot></div>
        <div class="nav-tag " v-show="type == 2" ></div>        
    </header>
</template>

<script>
export default {
    props:{
        type:{
            type:Number,
            default:1
        }
    },
    methods:{
        back() {
            this.$router.back()
        }
    }
}
</script>



